//region -------------------- Variables to customize Bootstrap --------------------

//$enable-shadows: true;
$enable-gradients: true;
$enable-negative-margins: true;
$enable-important-utilities: false;

//endregion -------------------- Variables to customize Bootstrap --------------------

@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/variables-dark';

//region -------------------- Custom variables --------------------

$grid-breakpoint-xs: #{map-get($grid-breakpoints, xs,)};
$grid-breakpoint-sm: #{map-get($grid-breakpoints, sm,)};
$grid-breakpoint-md: #{map-get($grid-breakpoints, md,)};
$grid-breakpoint-lg: #{map-get($grid-breakpoints, lg,)};
$grid-breakpoint-xl: #{map-get($grid-breakpoints, xl,)};
$grid-breakpoint-xxl: #{map-get($grid-breakpoints, xxl,)};

$border-width-1: #{map-get($border-widths, 1,)};
$border-width-2: #{map-get($border-widths, 2,)};
$border-width-3: #{map-get($border-widths, 3,)};
$border-width-4: #{map-get($border-widths, 4,)};
$border-width-5: #{map-get($border-widths, 5,)};

//endregion -------------------- Custom variables --------------------

//region -------------------- Override variables --------------------

//region -------------------- Spacer variables --------------------

$spacers-original: $spacers;
$spacers: (
  0: var(--bs-spacer-xs),
  1: var(--bs-spacer-sm),
  2: var(--bs-spacer-md),
  3: var(--bs-spacer-lg),
  4: var(--bs-spacer-xl),
  5: var(--bs-spacer-xxl),
);
$negative-spacers: (
  0: var(--bs-negative-spacer-xs),
  n1: var(--bs-negative-spacer-sm),
  n2: var(--bs-negative-spacer-md),
  n3: var(--bs-negative-spacer-lg),
  n4: var(--bs-negative-spacer-xl),
  n5: var(--bs-negative-spacer-xxl),
);
$gutters: (
  0: var(--bs-gutter-0),
  1: var(--bs-gutter-1),
  2: var(--bs-gutter-2),
  3: var(--bs-gutter-3),
  4: var(--bs-gutter-4),
  5: var(--bs-gutter-5),
);

//endregion -------------------- Spacer variables --------------------
//region -------------------- Border variables --------------------

$border-widths-original: $border-widths;
$border-widths: (
  1: var(--bs-border-width-1),
  2: var(--bs-border-width-2),
  3: var(--bs-border-width-3),
  4: var(--bs-border-width-4),
  5: var(--bs-border-width-5),
);
$border-radius-original: $border-radius;
$border-radius: var(--bs-border-radius);
$border-radius-sm-original: $border-radius-sm;
$border-radius-sm: var(--bs-border-sm-radius);
$border-radius-lg-original: $border-radius-lg;
$border-radius-lg: var(--bs-border-lg-radius);
$border-radius-xl-original: $border-radius-xl;
$border-radius-xl: var(--bs-border-xl-radius);
$border-radius-xxl-original: $border-radius-2xl;
$border-radius-2xl: var(--bs-border-xxl-radius);
$border-radius-pill-original: $border-radius-pill;
$border-radius-pill: var(--bs-border-pill-radius);
$border-radius-circle-original: 50%;
$border-radius-circle: var(--bs-border-circle-radius);

//endregion -------------------- Border variables --------------------
//region -------------------- Z-index variables --------------------

$z-index-dropdown-original: $zindex-dropdown;
$zindex-dropdown: var(--bs-z-index-dropdown);
$z-index-sticky-original: $zindex-sticky;
$zindex-sticky: var(--bs-z-index-sticky);
$z-index-fixed-original: $zindex-fixed;
$zindex-fixed: var(--bs-z-index-fixed);
$z-index-offcanvas-backdrop-original: $zindex-offcanvas-backdrop;
$zindex-offcanvas-backdrop: var(--bs-z-index-offcanvas-backdrop);
$z-index-offcanvas-original: $zindex-offcanvas;
$zindex-offcanvas: var(--bs-z-index-offcanvas);
$z-index-modal-backdrop-original: $zindex-modal-backdrop;
$zindex-modal-backdrop: var(--bs-z-index-modal-backdrop);
$z-index-modal-original: $zindex-modal;
$zindex-modal: var(--bs-z-index-modal);
$z-index-popover-original: $zindex-popover;
$zindex-popover: var(--bs-z-index-popover);
$z-index-tooltip-original: $zindex-tooltip;
$zindex-tooltip: var(--bs-z-index-tooltip);
$z-index-toast-original: $zindex-toast;
$zindex-toast: var(--bs-z-index-toast);

//endregion -------------------- Z-index variables --------------------
//region -------------------- Font-size variables --------------------

$font-size-sm-original: $font-size-sm;
$font-size-sm: var(--bs-font-size-sm);
